* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

body {
  line-height: 1;
}

body .services {
  position: relative;
  max-width: 900px;
  margin: 10px auto;
  width: 100%;
  max-width: 700px;
  overflow: hidden;
}

body .services .slider {
  width: calc(100% + 10px);
  overflow: hidden;
  box-sizing: border-box;
}

body .services .slider .wrap.s-notransition {
  transition: 0s;
}

body .services .slider .wrap {
  display: flex;
  transition: transform .5s;
  will-change: transform;
  z-index: 1;
  height: 150px;
}

body .services .slider .wrap .item {
  position: relative;
  display: flex;
  flex: 0 0 33.33333333%;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  padding-right: 10px;
}

body .services .slider .wrap .item .image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

body .services .slider .wrap .item .txt {
  position: absolute;
  display: block;
  padding: 20px;
  top: 0;
  left: 0;
  width: 150px;
  height: auto;
  color: #fff;
  font-family: sans-serif;
}

body .services .slider .wrap .item .txt a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s;
}

body .services .slider .wrap .item .txt a:hover {
  color: #0f0;
}

body .services .prev,
body .services .next {
  position: absolute;
  top: 50%;
  width: 50px;
  height: 50px;
  background: #fff;
  transform: translateY(-50%) translateZ(0);
  cursor: pointer;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  z-index: 3;
  background: #f00;
}

body .services .prev {
  left: 2px;
}

body .services .next {
  right: 2px;
}

body .services .radio {
  position: absolute;
  display: block;
  bottom: 10px;
  left: 50%;
  padding: 3px 15px;
  z-index: 3;
  transform: translateX(-50%) translateZ(0);
  background: #f00;
}

body .services .radio div {
  display: inline-block;
  margin: 0 5px;
  padding: 3px 6px;
  background-color: #fff;
  cursor: pointer;
}

body .services .radio div.active {
  background-color: #ff0;
}