* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
body {
    line-height: 1;
    .services {
        position: relative;
        max-width: 900px;
        margin: 10px auto;
        width: 100%;
        max-width: 700px;
        overflow: hidden;
        .slider {
            width: calc(100% + 10px);  // редактировать при смене к-ва блоков
            overflow: hidden;
            box-sizing: border-box;
            /*border: 1px solid;*/
            .wrap.s-notransition {
                transition: 0s;
            }
            .wrap {
                display: flex;
                transition: transform .5s;
                will-change: transform;
                z-index: 1;
                height: 150px; /* или 100% чтобы уменьш. пропорционально */
                .item {
                    position: relative;
                    display: flex;
                    flex: 0 0 33.33333333%; // ширина для количества блоков в карусели -  редактировать при смене к-ва блоков
                    overflow: hidden;
                    align-items: center;
                    justify-content: center;
                    padding-right: 10px; //  редактировать при смене к-ва блоков
                    .image {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        display: block;
                    }
                    .txt{
                        position: absolute;
                        display: block;
                        padding: 20px;
                        top: 0;
                        left: 0;
                        width: 150px;
                        height: auto;
                        color: #fff;
                        font-family: sans-serif;
                    }
                    .txt a{
                        color: inherit;
                        text-decoration: none;
                        transition: color 0.3s;
                        &:hover{
                            color: #0f0;
                        }
                    }
                }
            }
        }
        .prev,
        .next {
            position: absolute;
            top: 50%;
            /*left: 2px;*/
            width: 50px;
            height: 50px;
            background: #fff;
            transform: translateY(-50%) translateZ(0);
            cursor: pointer;
            text-indent: 100%;
            white-space: nowrap;
            overflow: hidden;
            z-index: 3;
            background: #f00;
        }
        .prev {
            left: 2px;
        }
        .next {
            right: 2px;
        }
        .radio{
            position: absolute;
            display: block;
            bottom: 10px;
            left: 50%;
            padding: 3px 15px;
            z-index: 3;
            transform: translateX(-50%) translateZ(0);
            background: #f00;
            div{
                display: inline-block;
                margin: 0 5px;
                padding: 3px 6px;
                background-color: #fff;
                cursor: pointer;
            }
            div.active{
                background-color: #ff0;
            }
        }
    }
}
